.footer {
    background-color: rgb(243, 243, 243);
    margin: 0px 0px;
    padding: 2% 2%;
    display: flex;
    align-items: stretch;
    flex-flow: row wrap;
    justify-content: space-between;
}

.ms-info {
    padding: 0 10%;
}

.copyright {
    font-family: myriad-pro,"Myriad Pro","Open Sans",Arial,sans-serif;
    font-size: 70%;
}

.logo {
    display: inline-block;
    min-width: 200px;
    max-width: 300px;
    height: auto;
    vertical-align: middle;
}

.icon {
    color: white !important;
    text-decoration: none;
    margin: 0.25rem;
    padding: 0.25rem;
    background-color: black;
    gap: 10px;
    border-radius: 5px;
    height:16px;
}

.icon:hover {
    cursor: pointer;
}

.social-svg {
    width: 16px;
    height:16px;
}

.socials {
    gap: 10px;
}

.slds-grid {
    display: inline-flex;
    flex-wrap: wrap;
    flex: 1 1 auto;
}

.sitemap {
    gap: 60px;
}

li {
    list-style: none;
}

.label-link {
    font-size: 120%;
    text-transform: uppercase;
    /* font-weight: 500; */
}

.site-nav {
    padding: 0% 10%;
}

.link {
    line-height: 1.8;
    text-decoration: none;
    color: black !important;
}

.link:hover {
    color: #d9232e !important;
}

.twitter:hover{
    background:#1da1f2;
}

.linkedin:hover {
    background:#0072b1;
}

.contact:hover {
    background:#d9232e;
}

.contact-label {
    position: relative; 
    bottom: 2px;
}
