* {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
}

.banner {
    background-image: url(../../static/media/banner.9f7d80ea.jpg);
    background-size: cover;
    padding: 3% 1% 3% 15%;
    margin-top: 10px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}

.welcome  {
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    margin: 5% 0%;
}

#headerwelcome {
    color: white;
    margin: 0;
    font-weight: 500;
}

#whatsnew {
    background-color: inherit;
    color: white;
    border: 1px solid white;
    font-size: 100%;
    margin-top: 20px;
    margin-left: 20px;
    padding: 10px 20px;
}

.video {
    display: inline-flex;
    flex-wrap: wrap;
    align-self: center;
    margin: 0% 5% 0% 2%;
}

iframe {
    width: 450px;
    height: 300px;
}

@media (max-width: 460px) {
    iframe {
        width: 250px;
        height: auto;
    }
}

.buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    grid-gap: 40px;
    gap: 40px;
}

.headers {
    margin: 0px;
}

.app {
    margin-bottom: 4%;
}

.padding-15 {
    padding: 1% 15% 2%;
}

.card:hover,
.card:focus, 
button:hover,
button:focus {
    -webkit-transform: scale(1.05) perspective(1px);
            transform: scale(1.05) perspective(1px);
    box-shadow: 2px 3px 5px 0 rgb(0 0 0 / 15%);
    cursor: pointer;
}

.navbar {
    display: flex;
    flex-direction: row;
    flex-wrap: no-wrap;
    justify-content: space-between;
    align-items:center;
    margin:10px 20px 10px 20px;
    height:40px;
    
    
}

@media screen and (max-width:800px) {

    /* Hide the big logo if width < 800px */
    #logo-img {
        display: none;
    }
   
}

@media screen and (min-width:801px) {

    /* Hide the small logo if width > 800px */
    #logo-img-small {
        display:none;
    
    }
}

#logo {
    flex: 0 0 140px;
    display: inline-flex;
}

#logo-img-small {
    height: 40px;
}

#logo-img {
    height: 40px;
}

#title {
    margin:10px;
    font-size: 1.25rem;
}
#search {
    flex: 6 6 400px;
    min-width: 150px;
    max-width:640px;
    margin:10px;
}

#search-bar {
    align-self: flex-start;
    height: 40px;
    width: 90%;
    background-color: rgba(236, 236, 236, 0.959);
    /* border:1px solid purple; */
    border:none;
    border-radius: 20px;;
    font-size: 90%;
    outline : none;
    padding: 0 0 0 20px;
    
    
}

#talk-to-expert {
    flex: 0 0 150px;
    font-family: myriad-pro,"Myriad Pro","Open Sans",Arial,sans-serif;
    background-color:#d9232e;
    color: white;
    border: 1px solid rgb(177, 11, 11);
    display: block;

    font-size:100%;
    padding: 10px;
    height: 40px;
    margin:10px;
    border-radius:5px;
}
/* 
@media screen and (min-width: 1100px) {
    #talk-to-expert { display: block; }
} */

.btn-talk-to-expert {
    cursor: pointer;
}
.sdk-button {
    width: 300px;
    height: 250px;
    border: 2px solid lightgray;
    border-radius: 10px;
    background-color: white;
    margin: 0;

}

.sdk-button-content {
    padding: 20px;
}

.sdk-name {
    vertical-align: top;
    margin: 0;
}

.sdk-desc {
    padding-top: 50px;
    margin: 0;
    line-height: 24px;
}

.sdk-button-hr {
    width: 30%;
    height:5px;
    border: 0px;
}

.section-header-name {
    margin: 0px 0px 15px;
    font-weight: 500;
}

.section-header {
    margin: 30px 5px;
}
.course-button {
    width: 300px;
    height: 270px;
    border: none;
    background-color: white;
    margin: 0;
    padding: 0px 0px;
}

#course-img {
    width: 300px;
    height: auto;
    margin: 0px;
}

.course-name {
    text-align: center;
    padding: 0px 10px;
    margin-top: 20px;
    line-height: 30px;
    font-weight: 500;
}

.course-desc {
    margin: 0;
    padding: 0;
}

h5 {
    margin-bottom: 0;
}
#more {
    margin: auto;
    min-width: 15%;
    display:block;
    margin-top: 40px;
    background-color: white;
    border: 2px solid darkgray;
    font-size: 110%;
    font-weight: 150%;
    padding: 10px 20px;
    color: #d9232e;
}
.footer {
    background-color: rgb(243, 243, 243);
    margin: 0px 0px;
    padding: 2% 2%;
    display: flex;
    align-items: stretch;
    flex-flow: row wrap;
    justify-content: space-between;
}

.ms-info {
    padding: 0 10%;
}

.copyright {
    font-family: myriad-pro,"Myriad Pro","Open Sans",Arial,sans-serif;
    font-size: 70%;
}

.logo {
    display: inline-block;
    min-width: 200px;
    max-width: 300px;
    height: auto;
    vertical-align: middle;
}

.icon {
    color: white !important;
    text-decoration: none;
    margin: 0.25rem;
    padding: 0.25rem;
    background-color: black;
    grid-gap: 10px;
    gap: 10px;
    border-radius: 5px;
    height:16px;
}

.icon:hover {
    cursor: pointer;
}

.social-svg {
    width: 16px;
    height:16px;
}

.socials {
    grid-gap: 10px;
    gap: 10px;
}

.slds-grid {
    display: inline-flex;
    flex-wrap: wrap;
    flex: 1 1 auto;
}

.sitemap {
    grid-gap: 60px;
    gap: 60px;
}

li {
    list-style: none;
}

.label-link {
    font-size: 120%;
    text-transform: uppercase;
    /* font-weight: 500; */
}

.site-nav {
    padding: 0% 10%;
}

.link {
    line-height: 1.8;
    text-decoration: none;
    color: black !important;
}

.link:hover {
    color: #d9232e !important;
}

.twitter:hover{
    background:#1da1f2;
}

.linkedin:hover {
    background:#0072b1;
}

.contact:hover {
    background:#d9232e;
}

.contact-label {
    position: relative; 
    bottom: 2px;
}

